/*
** Headers
*/

h1,
h2,
h3,
h4,
h5,
p {
  font-family: 'Roboto', sans-serif;
}

#myCanvas {
  background-color: #000000;
}

.header-text {
  color: white;
  font-size: 22px;
}

.button-text {
  font-size: 16px;
  color: black;
}

.window {
  padding-bottom: 25px;
  margin: auto;
  width: 655px;
  height: 700px;
  border: ridge 5px grey;
  border-color: white darkgrey darkgrey white;
  box-shadow: 10px 10px 6px -4px rgba(0, 0, 0, 0.22);
}

.header-bar {
  padding: 0px 5px;
  margin-bottom: 5px;
  text-align: center;
  line-height: 35px;
  height: 35px;
  vertical-align: middle;
  display: flex;
  justify-content: space-between;
  background-color: #000080;
}

.min-max-close-container {
  border: none;
  color: white;
  text-align: center;
  vertical-align: middle;
  display: inline-block;
  cursor: default;
  float: right;
}

.center-container {
  display: flex;
  justify-content: center;
}

.close-button {
  height: 25px;
  width: 25px;
  font-size: 22px;
  margin-top: 5px;
  justify-content: center;
  line-height: 0px;
  border: ridge 5px grey;
  border-color: white darkgrey darkgrey white;
}

.button {
  padding: 1px;
  height: 35px;
  width: 125px;
  position: absolute;
  bottom: 5px;
  border: ridge 5px grey;
  border-color: white darkgrey darkgrey white;
}

button:enabled:hover {
  background-color: #B2B2B2;
  color: black;
}

button:focus {
  background-color: #B2B2B2;
  color: black;
  border: ridge 5px grey;
  border-color: darkgrey white white darkgrey;
}

.background-gray {
  background-color: #B2B2B2;
}

pre {
  background-color: #000000;
  color: #FFFFFF;
  text-align: center;
  display: inline-block;
}

html,
body {
  padding: 25px 50px;
  background-color: #00807F
}
